import Game from "@/Game"

export default class Cheat {
  name: string
  description: string
  fun: (game: Game, args: any) => any

  constructor(name: string, description: string, cheatFunction: (game: Game, args: any) => any) {
    this.name = name
    this.description = description
    this.fun = cheatFunction
  }

  activate(game: Game) {
    // @ts-ignore
    window[this.name] = (args: any) => { return this.fun(game, args) }
  }
}
