import Cheat from "@/Cheat"
import Game from "@/Game"
import Card from "@/cards/Card"
import PickStage from "@/stages/PickStage"
import Deck from "@/cards/Deck"

export default class Cheatmode {
  private _game: Game
  private cheats: Cheat[] = [
    new Cheat("roll",
      `(value): Rolls all dice to this value.`,
      (game, value: number) => {
        game.state.stage.onDiceRoll([value])
      }),
    new Cheat("pickCard",
      `(name): Picks an unseen card from the current deck and adds it to your hand.`,
      (game: Game, name: string) => {
        let card = game.deck.allLevels.find((c: Card) => c.name == name)
        if (card != undefined) {
          game.state.pickedCards.addCard(card)
          return `Added ${card.name} to hand.`
        }
        return `Could not find the ${name} card in the current deck.`
      }),
    new Cheat("drawCard",
      `(name): Picks an unseen card from the current deck and adds it to the list of cards drawn from the deck.`,
      (game: Game, name: string) => {
        let card = game.deck.allLevels.find((c: Card) => c.name == name)
        if (card != undefined) {
          game.state.drawnCards.addCard(card)
          return `Added ${card.name} to hand.`
        }
        return `Could not find the ${name} card in the current deck.`
      }),
    new Cheat("redraw",
      `(value): Discard currently drawn cards and replace them with this many.`,
      (game, value: number) => {
        if (!(game.state.stage.id == 'draw' || game.state.stage.id == 'pick' || game.state.stage.id == 'bonus')) {
          return `Can not redraw cards in this stage.`
        }
        let cards = game.deck.draw(value)
        game.state.drawnCards = cards
        if (game.state.stage.id == 'pick') (game.state.stage as PickStage).deck = cards
        return `Drew ${value} cards, ${game.deck.size()} cards remain in the current deck.`
      }),
    new Cheat("deck",
      `(): Shows info about the current deck.`,
      (game, value: number) => {
        return `Deck size: ${game.state.deck.size()}, ${game.state.deck.allLevels.length}`
      }),
    new Cheat("unlockAllCards",
      `(): Permanently unlocks all cards in your collection.`,
      (game) => {
        game.menu.collection.addDeck(Deck.referenceDeck())
        game.menu.save()
        return `Done`
      }),
    new Cheat("clearSave",
      `(): Clears your save. Warning! your card collection will be lost.`,
      (game) => {
        game.menu.collection.removeAllCards()
        game.menu.save()
        return `Save cleared`
      })
  ]
  private activated: boolean = false

  constructor(game: Game) {
    this._game = game
    // @ts-ignore
    window.cheatmode = (password: string) => { return this.activateCheats(password) }
  }

  set game(game: Game) {
    this._game = game
    this.registerCheats()
  }

  activateCheats(password: string): string {
    if (password != "rose") return "Wrong password. Find out the current password by becoming a Patreon supporter!"
    this.activated = true
    this.registerCheats()

    let s = "Cheatmode activated. Available commands: \n\n"
    for (let cheat of this.cheats) {
      s += `${cheat.name}${cheat.description}\n`
    }
    return s
  }

  private registerCheats() {
    if (!this.activated) return false

    for (let cheat of this.cheats) {
      cheat.activate(this._game)
    }
  }
}
